function slider() {
	// -------- Слайдер ---------------------------------------------
	const slides = document.querySelectorAll('.offer__slide'),
		slider = document.querySelector('.offer__slider'),
		prev = document.querySelector('.offer__slider-prev'),
		next = document.querySelector('.offer__slider-next'),
		total = document.querySelector('#total'),
		current = document.querySelector('#current'),
		slidesWrapper = document.querySelector('.offer__slider-wrapper'),
		slidesField = document.querySelector('.offer__slider-inner'),
		width = window.getComputedStyle(slidesWrapper).width; // получаем ширину окна слайдера

	let slideIndex = 1;
	let offset = 0;

	if (slides.length < 10) {
		total.textContent = `${slides.length}`;
		current.textContent = `${slideIndex}`;
	} else {
		total.textContent = slides.length;
		current.textContent = slideIndex;
	}

	slidesField.style.width = 100 * slides.length + '%';
	slidesField.style.display = 'flex';
	slidesField.style.transition = '0.5s all';

	slidesWrapper.style.overflow = 'hidden';

	slides.forEach((slide) => {
		slide.style.width = width;
	});
	// Точки

	slider.style.position = 'relative';

	const dots = document.createElement('ol');
	const dotsArr = [];
	dots.classList.add('carousel-dots');
	dots.style.cssText = `
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 15;
		display: flex;
		justify-content: center;
		margin-right: 15%;
		margin-left: 15%;
		list-style: none;
	`;
	slider.append(dots);

	for (let i = 0; i < slides.length; i++) {
		const dot = document.createElement('li');
		dot.setAttribute('data-slide-to', i + 1);
		dot.style.cssText = `
			box-sizing: content-box;
			flex: 0 1 auto;
			width: 30px;
			height: 6px;
			margin-right: 3px;
			margin-left: 3px;
			cursor: pointer;
			background-clip: padding-box;
			border-top: 10px solid transparent;
			border-bottom: 10px solid transparent;
			background-color: #fff;
			opacity: 0.3;
			transition: opacity 0.6s ease;
	`;
		if (i == 0) {
			dot.style.opacity = 1;
		}
		dots.append(dot);
		dotsArr.push(dot);
	}

	// Второй способ при помощи Регулярных выражений
	function deleteNotDigits(str) {
		return +str.replace(/\D/g, '');
	}

	// Первый способ - Вырезаем у строки, например, 200px - px (это первый способ через slise)

	// next.addEventListener('click', () => {
	// 	if (offset == +width.slice(0, width.length - 2) * (slides.length - 1)) {
	// 		offset = 0;
	// 	} else {
	// 		offset += +width.slice(0, width.length - 2);
	// 	}

	next.addEventListener('click', () => {
		if (offset == deleteNotDigits(width) * (slides.length - 1)) {
			offset = 0;
		} else {
			offset += deleteNotDigits(width);
		}

		slidesField.style.transform = `translateX(-${offset}px)`;

		if (slideIndex == slides.length) {
			slideIndex = 1;
		} else {
			slideIndex++;
		}

		if (slides.length < 10) {
			current.textContent = `${slideIndex}`;
		} else {
			current.textContent = slideIndex;
		}

		dotsArr.forEach((dot) => (dot.style.opacity = '0.3'));
		dotsArr[slideIndex - 1].style.opacity = 1;
	});

	prev.addEventListener('click', () => {
		if (offset == 0) {
			offset = deleteNotDigits(width) * (slides.length - 1);
		} else {
			offset -= deleteNotDigits(width);
		}

		slidesField.style.transform = `translateX(-${offset}px)`;

		if (slideIndex == 1) {
			slideIndex = slides.length;
		} else {
			slideIndex--;
		}

		if (slides.length < 10) {
			current.textContent = `${slideIndex}`;
		} else {
			current.textContent = slideIndex;
		}

		dotsArr.forEach((dot) => (dot.style.opacity = '0.3'));
		dotsArr[slideIndex - 1].style.opacity = 1;
	});

	dotsArr.forEach((dot) => {
		dot.addEventListener('click', (e) => {
			const slideTo = e.target.getAttribute('data-slide-to');

			slideIndex = slideTo;
			offset = deleteNotDigits(width) * (slideTo - 1);

			slidesField.style.transform = `translateX(-${offset}px)`;

			if (slides.length < 10) {
				current.textContent = `${slideIndex}`;
			} else {
				current.textContent = slideIndex;
			}

			dotsArr.forEach((dot) => (dot.style.opacity = '.3'));
			dotsArr[slideIndex - 1].style.opacity = 1;
		});
	});

	// Самый простой слайдер
	// showSlides(slideIndex);

	// if (slides.length < 10) {
	// 	total.textContent = `${slides.length}`;
	// } else {
	// 	total.textContent = slides.length;
	// }

	// function showSlides(n) {
	// 	if (n > slides.length) {
	// 		slideIndex = 1;
	// 	}
	// 	if (n < 1) {
	// 		slideIndex = slides.length;
	// 	}

	// 	slides.forEach((item) => (item.style.display = 'none'));

	// 	slides[slideIndex - 1].style.display = 'block';

	// 	if (slides.length < 10) {
	// 		current.textContent = `${slideIndex}`;
	// 	} else {
	// 		current.textContent = slideIndex;
	// 	}
	// }

	// function plusSlides(n) {
	// 	showSlides((slideIndex += n));
	// }

	// prev.addEventListener('click', () => {
	// 	plusSlides(-1);
	// });

	// next.addEventListener('click', () => {
	// 	plusSlides(1);
	// });
}

module.exports = slider;
